import * as React from 'react';
import { VPage, Page, BoxId, Form, UiRadio, UiSchema, UiTextItem, UiInputItem, UiIdItem, Schema, Context, tv } from 'tonva';
import { observer } from 'mobx-react';
import { SupplierItem } from "model/supplierItem";
import { CPackage } from './CPackage';
import { tariffRateValidation } from 'tools/inputValidations';

export class VPackage extends VPage<CPackage> {
    private form: Form;
    private item: any;
    private parent: any;
    async open(param?: SupplierItem) {
        let { item, parent } = param;
        this.item = item;
        this.parent = parent;
        this.openPage(this.page);
    }

    private schema: Schema = [
        { name: 'quantity', type: 'number', required: true },
        { name: 'radiox', type: 'number', required: true },
        { name: 'radioy', type: 'number', required: true },
        { name: 'unit', type: 'id', required: true },
        { name: 'purity', type: 'string', required: false },
        { name: 'type', type: 'string', required: true },
        { name: 'listPrice', type: 'number', required: false },
        { name: 'price', type: 'number', required: true },
        { name: 'currency', type: 'id', required: true },
        { name: 'isTaxIn', type: 'number', required: true },
        { name: 'invoiceType', type: 'number', required: true },
        { name: 'vatRate', type: 'id', required: false },
        { name: 'tariffRate', type: 'number', required: false },
        { name: 'isTransFeeIn', type: 'number', required: true },
        { name: 'transFee', type: 'number', required: false },
        { name: 'transFeecurrency', type: 'id', required: false },
        { name: 'packingFee', type: 'number', required: false },
        { name: 'packingcurrency', type: 'id', required: false },
        { name: 'otherFee', type: 'number', required: false },
        { name: 'otherFeecurrency', type: 'id', required: false },
        { name: 'customizeUpto', type: 'date', required: false },
        { name: 'validUpto', type: 'date', required: true },
        { name: 'minArriveDate', type: 'date', required: false },
        { name: 'maxArriveDate', type: 'date', required: false },
        { name: 'remark', type: 'string', required: false },
        { name: 'submit', type: 'submit' }
    ];

    private uiSchema: UiSchema = {
        items: {
            quantity: { widget: 'text', label: '', placeholder: '', defaultValue: 1 } as UiInputItem,
            radiox: { widget: 'text', label: '', placeholder: '', defaultValue: 1 } as UiInputItem,
            radioy: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            unit: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickPackUnit(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            purity: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            type: { widget: 'radio', label: '', list: [{ value: 1, title: '' }, { value: 2, title: '' }] } as UiRadio,
            listPrice: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            price: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            currency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.cApp.cPendingInquiry.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            isTaxIn: { widget: 'radio', label: '', list: [{ value: "0", title: '' }, { value: "1", title: '' }] } as UiRadio,
            invoiceType: { widget: 'radio', label: '', list: [{ value: "1", title: '' }, { value: "2", title: '' }, { value: "3", title: '' }, { value: "4", title: '' }] } as UiRadio,
            vatRate: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickVatRate(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.description * 100}</>)}%
                    </>;
                }
            } as UiIdItem,
            tariffRate: { widget: 'text', label: '', placeholder: '', rules: tariffRateValidation } as UiInputItem,
            isTransFeeIn: { widget: 'radio', label: '', list: [{ value: "0", title: '' }, { value: "1", title: '' }] } as UiRadio,
            transFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            transFeecurrency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.cApp.cPendingInquiry.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            packingFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            packingcurrency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.cApp.cPendingInquiry.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            otherFee: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            otherFeecurrency: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.cApp.cPendingInquiry.pickCurrency(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.name}</>)}
                    </>;
                }
            } as UiIdItem,
            customizeUpto: { widget: 'date', label: '' } as UiInputItem,
            validUpto: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            minArriveDate: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            maxArriveDate: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            remark: { widget: 'textarea', label: '', placeholder: '', rows: 2 } as UiInputItem,
            submit: { widget: 'button', label: '', className: "btn btn-primary mr-3 px-6" }
        }
    };

    private onFormButtonClick = async (name: string, context: Context) => {
        let { savePackage } = this.controller;
        let id = this.item && this.item.id;
        await savePackage(id, context.form.data, this.parent);
    }

    private page = observer(() => {

        return <Page header="" headerClassName="bg-primary">
            <div className="App-container container text-left">
                <Form ref={v => this.form = v} className="my-3"
                    formData={this.item}
                    schema={this.schema}
                    uiSchema={this.uiSchema}
                    onButtonClick={this.onFormButtonClick}
                    requiredFlag={true}
                />
            </div>
        </Page>;
    })
}

