import * as React from 'react';
import { VPage, Page, BoxId, Form, UiRadio, UiSchema, UiSelect, UiInputItem, UiIdItem, Schema, Context, tv } from 'tonva';
import { observer } from 'mobx-react';
import { CProduct } from './CProduct';
import _ from 'lodash';

const schema: Schema = [
    { name: 'id', type: 'id', required: false },
    { name: 'isspot', type: 'number', required: false },
    { name: 'storageRange', type: 'id', required: false },
    { name: 'expiry', type: 'string', required: false },
    { name: 'expiryUnit', type: 'string', required: false },
    { name: 'restrictMark', type: 'id', required: false },
    { name: 'refractiveIndex', type: 'string', required: false },
    { name: 'opticalRotation', type: 'string', required: false },
    { name: 'flashPoint', type: 'string', required: false },
    { name: 'meltingPoint', type: 'string', required: false },
    { name: 'boilingPoint', type: 'string', required: false },
    { name: 'character', type: 'string', required: false },
    { name: 'chroma', type: 'string', required: false },
    { name: 'waterContent', type: 'string', required: false },
    { name: 'useFor', type: 'string', required: false },
    { name: 'remark', type: 'string', required: false },
    { name: 'submit', type: 'submit' }
];

export class VProductProperty extends VPage<CProduct> {
    private form: Form;
    private product: any;

    async open(param?: any) {
        this.product = param;
        this.openPage(this.page);
    }

    private uiSchema: UiSchema = {
        items: {
            id: { visible: false },
            isspot: { widget: 'radio', label: '', list: [{ value: 1, title: '' }, { value: 0, title: '' }] } as UiRadio,
            storageRange: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickStorageRange(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.description}</>)}
                    </>;
                }
            } as UiIdItem,
            expiry: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            expiryUnit: { widget: 'select', label: '', list: [{ value: '', title: '' }, { value: '', title: '' }, { value: '', title: '' }, { value: '', title: '' }] } as UiSelect,
            restrictMark: {
                widget: 'id', label: '', placeholder: '',
                pickId: async (context: Context, name: string, value: number) => await this.controller.pickRestrictMark(context, name, value),
                Templet: (item: any) => {
                    if (!item) return <small className="text-muted"></small>;
                    return <>
                        {tv(item, v => <>{v.description}</>)}
                    </>;
                }
            } as UiIdItem,
            refractiveIndex: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            opticalRotation: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            flashPoint: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            meltingPoint: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            boilingPoint: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            character: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            chroma: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            density: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            waterContent: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            useFor: { widget: 'textarea', label: '', placeholder: '', rows: 2 } as UiInputItem,
            remark: { widget: 'textarea', label: '', placeholder: '', rows: 2 } as UiInputItem,
            submit: { widget: 'button', label: '', className: "btn btn-primary mr-3 px-6" }
        }
    };

    private onFormButtonClick = async (name: string, context: Context) => {
        await this.controller.saveProductProperty(context.form.data, this.product);
    }

    private page = observer(() => {
        let descriptionData = _.clone(this.product);

        return <Page header="" headerClassName="py-1 bg-primary">
            <div className="App-container container text-left">
                <Form ref={v => this.form = v} className="my-3"
                    schema={schema}
                    uiSchema={this.uiSchema}
                    formData={descriptionData}
                    onButtonClick={this.onFormButtonClick}
                    fieldLabelSize={3}
                />
            </div>
        </Page>
    })
}

